import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, ObjectKeys, qryStore } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt, SvgIcon_Cmpt } from "../../../components";


import "./styles.css"
import { CloseModal } from "../../../components/DialogHRM";
import { SelectDropAccount_Cmpt } from "..";

const Svg = SvgIcon_Cmpt();

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();

const DropInput = SelectDropAccount_Cmpt();




const frmNm = "new_entry_comprobante"

const AddClient  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		ffm["accountingEntryId"] = gen16CodeId();

		ffm["accountsToParameterize"] = getStore("accountListOnParam");

		let bdyq2 = {...getStore(qryStore())?.add_entries};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId
		}
		bdyq2.form=ffm;

		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			updStore("accountListOnParam",null)
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
	};





	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	

	const handleSearchAccount = (acc,subacc) => {
		let nn= {
			"account":acc,
			"subAccount":subacc
		}
		updStore("account2Param", nn);
		set_obs(gen16CodeId())
	}


	const editSearchAccount = () => {
		updStore("account2Param", null);
		set_obs(gen16CodeId())
	}

	
	const updFrmAccounts = (f, v, k) => {
		let frm4 = {...getStore("account2Param")} || {};
		if(k){
			if( !frm4[k]){
				frm4[k] = {};
			}
			frm4[k][f] = v;
		}else{
			frm4[f] = v;
		}
		updStore("account2Param", frm4);
		set_obs(gen16CodeId())
	};

	const addAccount2List = () => {
		let f2p = {...getStore("account2Param")} || {};
		if(!f2p.type){
			f2p.type = "debit"
		}
		let accountListOnParam = {...getStore("accountListOnParam")} || {};
		let id = f2p["account"] + "_"+ f2p["subAccount"];
		accountListOnParam[id] = f2p;
		updStore("accountListOnParam", accountListOnParam);
		updStore("account2Param", null);
		set_obs(gen16CodeId())
	}

	const rmvAccount2List = (id) => {
		let accountListOnParam = {...getStore("accountListOnParam")} || {};
		delete accountListOnParam[id];
		updStore("accountListOnParam", accountListOnParam);
		set_obs(gen16CodeId())
	}

	let accountD = getStore("account_ids")?.[getStore("account2Param")?.account];
	let subaccountD = getStore("sub_account_ids")?.[getStore("account2Param")?.subAccount];
	

	return (
		<div style={{
			maxHeight: "80vh",
			width: "960px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar Comprobante Parametrizado
				</p>
				<div className="flexSpace"/>
				
			</div>

			
			<TextInput  
				init={getStore(frmNm)?.["name"]} 
				label={"Nombre"} 
				width={520} 
				updChanges={(e)=>updFrm("name",e)}
			/>

			<div className="_dsplFlx " style={{padding: "18px"}}>
				<div className="flexSpace"/>
				<div>

					<div className="label">{"Agregar extras"}</div>
					<CheckBox 
						data={[
							{label: "Empleados", id:"employee"},
							{label: "Provedores", id:"provider"},
							{label: "Clientes", id:"customer"},
							{label: "Recurrente", id:"recurrent"},
						]}
						active={getStore(frmNm)?.["extra"]}
						updSelect={(e)=>updFrm("extra",e)}
					/>
				</div>
				<div className="flexSpace"/>
				
			</div>


			<div className="separator"/>
			<div className=" " style={{paddingTop: "18px"}}>
			{getStore("account2Param")?.account ? 
				<div className="_dsplFlx " style={{margin: "18px 0"}}>
					<div className=" acc_dtls">
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff"}}>
								{accountD?.code} 
							</div>
							<div className="name">
								{accountD?. name}
							</div>
						</div>
						<div className="flexSpace"/>
						{subaccountD ?
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
							<div className="code" >
								{subaccountD?.subAccount} 
							</div>
							<div className="name">
								{subaccountD?. subAccountName}
							</div>
						</div>
						:null}
					</div>
					<div className="flexSpace"/>
					<div className="icon" onClick={editSearchAccount}>
						<Svg name={"outline_edit"} color={"#1a73e8"}/>
					</div>
				</div> :
				<DropInput 
					
					placeholder={"buscar una cuenta"} 
					width={420}
					handleClick={handleSearchAccount}
				/>
			}
			</div>
			<div className="_dsplFlx " style={{padding: "18px"}}>
				
				{getStore(frmNm)?.["extra"]==="recurrent"?
					<>
					<div className="flexSpace"/>
					<div className="_dsplFlx " >
						<TextInput  
							init={getStore("account2Param")?.["document"]} 
							label={"Documento"} 
							width={220} 
							updChanges={(e)=>updFrmAccounts("document",e)}
						/>
						<div className="flexSpace" style={{margin:"0 11px"}}/>
						<TextInput  
							init={getStore("account2Param")?.["amount"]} 
							label={"Importe"} 
							width={120} 
							type={"number"}
							updChanges={(e)=>updFrmAccounts("amount",e)}
						/>
					</div>
					</>
				:null}
				<div className="flexSpace"/>
				<CheckBox 
					data={[
						{label: "Deudoras", id:"debit"},
						{label: "Acredoras", id:"credit"}
						
					]}
					active={getStore("account2Param")?.["type"] || "debit"}
					updSelect={(e)=>updFrmAccounts("type",e)}
				/>
				<div className="flexSpace"/>
				<div>
					<button onClick={addAccount2List}>
						Agregar
					</button>
				</div>
			</div>

			
			

			<div className="_dsplFlx hdr_bar purple" style={{margin: "18px 0"}}>
					<div className=" tr_bar tr_bar_20">
						Cuenta
					</div>
					<div className="tr_bar tr_bar_20">
						Sub Cuenta
					</div>
					<div className="tr_bar tr_bar_20">
						Tipo
					</div>
					<div className="tr_bar tr_bar_20">
						Documento
					</div>
					<div className="tr_bar tr_bar_20">
						Importe
					</div>

			</div>
			{getStore("accountListOnParam") && ObjectKeys(getStore("accountListOnParam"))?.map((element, indE)=>
					<ListingAccounts key={element} idKey={element} data={getStore("accountListOnParam")[element]} deleteItem={rmvAccount2List}/>
			)}

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>

			</div>
		

			
		</div>
	);
};

export default AddClient;

const ListingAccounts  = ({ data, deleteItem, idKey }) => {
	let accountD = getStore("account_ids")?.[data?.account];
	let subaccountD = getStore("sub_account_ids")?.[data?.subAccount];

	const editSearchAccount = () => {
		deleteItem(idKey)
	}
	
	return (
		<div className="_dsplFlx hdr_bar">
			<div className="tr_bar_20">
				{accountD?.code}
			</div>
			<div className="tr_bar_20">
				{subaccountD?.subAccount}
			</div>
			<div className="tr_bar_20">
				{data?.type}
			</div>
			<div className="tr_bar_20">
				{data?.document}
			</div>
			<div className="tr_bar_20">
				{data?.amount}
			</div>
			<div className="flexSpace"/>
			<div className="icon" onClick={editSearchAccount}>
				<Svg name={"outline_delete"} color={"#c62828"}/>
			</div>

		</div>
	)

}

